@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
body{min-width:360px;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{box-sizing:border-box}p,h1,h2,h3,h4,h5{margin:0}.mt-15{margin-top:15px}.m-15{margin:15px}.row{display:flex}.column{flex-direction:column}.justify-content-between{justify-content:space-between}.align-items-center{align-items:center}.justify-content-center{justify-content:center}.Menu{padding:10px 24px;min-height:25px;background-color:#eeeeee;display:flex;justify-content:center;margin-top:130px}.MenuItems{color:#616161;margin:0 20px;display:inline-block;position:relative;font-weight:500;text-transform:uppercase;text-decoration:none;cursor:pointer}.MenuItems::after{color:#6600ff;width:0;height:3px;content:'';display:block;position:absolute;box-sizing:border-box;transition:0.5s;border-bottom:1px solid #6600ff}.heading-page{color:#424242;font-size:22px;font-family:"Roboto", "Helvetica", "Arial", sans-serif;font-weight:700;line-height:1.167;margin-bottom:30px;letter-spacing:0em;text-transform:uppercase}.container{max-width:1280px;width:100%;box-sizing:border-box;margin-left:auto;margin-right:auto;padding-left:32px;padding-right:32px;padding-top:30px;padding-bottom:30px}.btn-icon{width:23px;height:23px;padding:0;display:inline-block;background-color:#ffffff;border:none;outline:none;cursor:pointer}.notification{font-size:24px;line-height:70px;font-weight:700;color:#000;font-family:FontBrandedPrimary, FontBrandedSecondary, FontBrandedFallback, sans-serif}

.btn{font-family:"Roboto", "Helvetica", "Arial", sans-serif;font-size:14px;font-weight:600;min-width:130px;display:inline-block;color:#fff;background-color:green;text-align:center;border:1px solid transparent;padding:6px 16px;line-height:1.75;margin:0 15px;border-radius:10px;cursor:pointer;transition:0.3s linear}.btn:hover{transform:scale(1.1)}.btn-modal{margin:10px}

p{padding-top:15px}.Product{width:287px;height:100%;margin:16px;color:rgba(0,0,0,0.87);background-color:#fff;display:inline-block;padding:20px;position:relative;overflow:hidden;border-radius:10px;box-shadow:2px 3px 7px 0px rgba(0,0,0,0.24);transition:0.3s linear}.Product:hover{transform:scale(1.05)}.Product__NameAlbums{font-weight:500;font-size:17px}.Product__Author{font-weight:500;font-size:14px}.Product__Img{height:200px;object-fit:cover;width:90%;display:block;border-radius:10px;background-size:cover;background-repeat:no-repeat;background-position:center}.Product__Btn{width:23px;height:23px;padding:0;display:inline-block;background-color:#fff;border:none;outline:none}.Product__Type{font-size:14px;margin-right:7px}.Product__Price{font-size:21px;font-weight:700;color:blue}.Product__Сurrency{font-size:16px}.Product__Discount{font-size:14px;color:grey;text-decoration:line-through}.Product__id{font-size:12px}

.cart-img{width:220px;object-fit:cover;display:block;border-radius:10px;background-size:cover;background-repeat:no-repeat;background-position:center}.width{max-width:1100px;margin:0 auto}.hr{max-width:1100px}@media (max-width: 1000px){.price-count-btn{display:flex;flex-direction:column}}@media (max-width: 725px){.container-all{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:0}.cart-img{width:100%}.price-count-btn{display:flex;flex-direction:row}}@media (max-width: 600px){.price-count-btn{display:flex;flex-direction:column}}

.wrapper{width:100%;height:100vh;position:fixed;top:0;left:0;z-index:100;background-color:rgba(0,0,0,0.3)}.modal{text-align:center;position:fixed;color:#fff;overflow:hidden;outline:0;left:calc(50% - 163px);top:300px;z-index:777}.modal-header{position:relative;font-size:28px;width:330px;display:flex;align-items:flex-start;justify-content:space-between;padding:25px;border-top-left-radius:10px;border-top-right-radius:10px}.modal-body{width:330px;position:relative;padding:10px}.modal-body .text-modal{padding-top:20px;margin:0}.modal-footer{width:330px;padding-top:10px;display:flex;align-items:center;justify-content:center;padding-bottom:20px;border-bottom-right-radius:10px;border-bottom-left-radius:10px}.close{position:absolute;top:0;right:0;z-index:2;padding:7px 20px 0 0;float:right;font-size:50px;font-weight:100;line-height:1;color:#fff;text-shadow:0 1px 0 #555454}.close:hover{color:red;text-decoration:none;cursor:pointer}

.container-all--all{padding-top:170px}@media (max-width: 725px){.container-all--all{padding-top:270px}}@media (max-width: 395px){.container-all--all{padding-top:330px}}

.notification{font-size:20px}

.container{max-width:1680px}.container-cart{padding-top:200px}.hr{max-width:1100px}

.formField{width:250px;font-size:18px;margin-top:5px;padding-right:5px}.error{color:red}.containerAll{max-width:400px;margin:0 auto;display:flex;justify-content:center;align-items:center}.containerInput,.delivery{display:flex;flex-direction:column;justify-content:center;align-items:center}.buttonContainer{display:flex;justify-content:center;align-items:center;padding-top:10px}@media (max-width: 475px){.formField{width:280px}.containerAll{flex-direction:column}}

.Favorite__Btn{width:25px;height:25px;padding-left:10px;display:inline-block;background-color:#fff;border:none}

.ProductList{max-width:1600px;display:flex;flex-wrap:wrap;padding-top:150px;align-items:center;align-content:center;margin-bottom:30px;box-sizing:border-box;margin-left:auto;margin-right:auto;justify-content:center}@media (max-width: 725px){.ProductList{padding-top:270px}}@media (max-width: 395px){.ProductList{padding-top:340px}}

.Logo{height:100px;transition:0.3s linear}.Logo:hover{transform:scale(1.1)}

.headerStyled{display:flex;justify-content:space-between;padding-left:24px;padding-right:24px;background-color:#fff;position:relative;align-items:center;min-height:130px;top:0;left:auto;position:fixed;width:100%;z-index:99;box-sizing:border-box}.headerStyled__Heading{font-family:"Fredoka One","Helvetica","Arial",sans-serif;font-size:50px;font-weight:400;text-transform:uppercase;text-align:center;color:red}@media (max-width: 725px){.headerStyled{flex-direction:column;margin:0;padding:10px}}

.Menu{box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12);transition:box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;background-color:#fff;position:relative;align-items:center;min-height:24px;top:0;left:auto;position:fixed;width:100%;z-index:99;box-sizing:border-box}.MenuItems{font-family:"Fredoka One","Helvetica","Arial",sans-serif;font-size:24px;color:red;transition:0.3s linear}.MenuItems:hover,.MenuItems:focus,.MenuItems:active{color:green;fill:green}@media (max-width: 725px){.Menu{top:90px}.MenuItems{display:flex;flex-direction:column;justify-content:center;align-items:center}}@media (max-width: 395px){.Menu{top:150px}}

